export module CalculatorRowGenerator {
  export function renderTableRows(rows: number) {
  //render the base state of the table with number of base rows 
    var returnRows: string;
    for(var i = 1; i <= rows; i++) {
      returnRows += `
        <tr class="classrow">
          <td><input title="Class ` + i + `" class="coursename" type="text" /></td>
          <td><input title="Credit Hours for class ` + i + `" class="credithours" type="number" min="1" max="6" /></td>
          <td><select class="lettergrade" title="Letter grade for class ` + i + `"><option value="4.0">A</option><option value="3.667">A-</option><option value="3.333">B+</option><option value="3.0">B</option><option value="2.667">B-</option><option value="2.333">C+</option><option value="2.0">C</option><option value="1.667">C-</option><option value="1.33">D+</option><option value="1.0">D</option><option value="0.0">F</option></select></td>
        </tr>
      `;
    } //end for
    return returnRows;
  } //end function

  export function deleteTableRows(rows: number) {
    //TODO: add functionality to remove table rows
    return;
  } //end function
}


