import $ = require('jquery');
import { CalculatorRowGenerator } from './CalculatorRowMethods';
import { GPACalculations } from './CalculatorMathFunctions';

export module GPACalculator {
  
  export class CalculatorValues {
    credits: number; 
    gradePoints: number;
    totalGP: number;
    numRows: number;
    baseTable: string;
    tableRoot: string;

    setCredits(value: number) {
      this.credits = value;
    }

    setGradePoints(value: number) {
      this.gradePoints = value;
    }

    setTotalGP(value: number) {
      this.totalGP = value;
    }

    setNumRows(value: number) {
      this.numRows = value;
    }

    getBaseTable(): string {
      return this.baseTable;
    }

    constructor(tableAnchor: string, startingRows?: number) {
      this.tableRoot = tableAnchor;
      this.baseTable = `
        <table id="calculatorTable">
          <thead>
            <tr>
              <th>Course Name</th><th>Credit Hours</th><th>Letter Grade</th>
            </tr>
          </thead>
          <tfoot>
          <tr>
            <td><input type="submit" class="update-calculator" value="Update" /></td>
            <td><button class="remove-row">Remove Row</button></td>
            <td><button class="reset">Clear Rows</button></td>
          </tr>
        </tfoot>
        </table>
        <p class="total-credits">Total Credits: 0</p>
        <p class="total-grade-points">Total Grade Points: 0</p>
        <p class="total-gpa">Total GPA: 0.00</p>
        `;

      this.setCredits(0);
      this.setGradePoints(0.00);
      this.setTotalGP(0);

      if(typeof(startingRows) !== 'undefined') {
        this.numRows = startingRows;
      } else {
        this.numRows = 1;
      }
      //console.dir(this);
      $(tableAnchor).append(this.baseTable); 
      this.addRows(this.numRows);

      //click handler for "update" button
      var self = this; // save scope before entering callback
      $('.update-calculator').click(function() {
        //update the credits calculation
        //console.info(GPACalculations.totalCredits());
        var tc = GPACalculations.totalCredits();
        $('.total-credits').html('Total Credits: ' + tc);
        self.setCredits(tc);

        //update the total GPA
        var gp = GPACalculations.totalGradePoints();
        $('.total-grade-points').html('Total Grade Points: ' + gp.toFixed(2));
        self.setGradePoints(gp);

        //update the GPA calculation
        var gpa = GPACalculations.totalGPA(tc, gp);
        $('.total-gpa').html('Total GPA: ' + gpa);
        self.setTotalGP(parseFloat(gpa));
      });

      return;

    }

    addRows(numRows: number): boolean {
      $(this.tableRoot + " table").append(CalculatorRowGenerator.renderTableRows(numRows));
      return true; 
    }

    removeRows(numRows: number): boolean {
      //TODO: method to remove table rows
      return true;
    }


  } //end class
}



