export module GPACalculations {

  export function totalCredits(): number {
    var totalCreditsCount = 0;
    for(var i = 0; i < document.getElementsByClassName('credithours').length; i++) { 
      totalCreditsCount += parseInt((<HTMLInputElement>document.getElementsByClassName('credithours')[i]).value);
      
    }
    return totalCreditsCount;
  }// end function

  export function totalGradePoints(): number {
    var rowGradePoints = 0;
    var totalGradePoints = 0;
    for(var i=0; i < document.getElementsByClassName('lettergrade').length; i++) {
      
      rowGradePoints = parseFloat((<HTMLInputElement>document.getElementsByClassName('lettergrade')[i]).value) * parseInt((<HTMLInputElement>document.getElementsByClassName('credithours')[i]).value);
      totalGradePoints += rowGradePoints;
    }
    
    return totalGradePoints;
  }

  export function totalGPA(credits: number, gradePoints: number): string {
    var totalGPA = '';

    totalGPA = (gradePoints / credits).toFixed(2);
    
    return totalGPA;
  }
} // end module


